.card {
  flex-direction: column;
  width: 200px;
  padding: 33px 17px 17px;
  display: flex;
}

.card__image {
  background-repeat: no-repeat;
  background-size: contain;
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__nameProduct {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__codeProduct {
  color: #616070;
  margin: 4px 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin-top: 12px;
  display: flex;
}

.card__reviews {
  text-align: center;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  margin: 24px 0 16px;
  display: flex;
}

.price-name {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars:last-child {
  margin-right: 0;
}

.stars--4 > .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

